<template>
  <BaseView :actionButtons="buttonColors" @action-button-click="onButtonTap">
    <div class="play-header">
      <h1>{{ trainingFileData.title }}</h1>
      <p>Level {{ currentLevel.level }}</p>
    </div>

    <div class="play-content">
      <div class="play-status">
        <p v-if="isLoading">Loading song data...</p>
        <p v-else-if="isPlaying"> Playing...</p>
        <p v-else-if="hasStarted && showReport">Training completed!</p>
        <p v-else-if="hasStarted">Training completed!</p>
        <p v-else>Ready to play!</p>
      </div>

      <!-- Training Report -->
      <div v-if="hasStarted && !isPlaying && showReport" class="training-report">
        <h3>Training Results</h3>
        <div class="report-stats">
          <div class="stat-item">
            <span class="stat-label">Total Segments:</span>
            <span class="stat-value">{{ reportData.summary.totalSegments }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">Correct:</span>
            <span class="stat-value correct">{{ reportData.summary.correct }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">Wrong:</span>
            <span class="stat-value wrong">{{ reportData.summary.wrong }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">Missed:</span>
            <span class="stat-value miss">{{ reportData.summary.miss }}</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">Accuracy:</span>
            <span class="stat-value">{{ reportData.summary.accuracy }}%</span>
          </div>
          <div class="stat-item">
            <span class="stat-label">Duration:</span>
            <span class="stat-value">{{ Math.round(reportData.duration / 1000) }}s</span>
          </div>
        </div>
      </div>

      <!-- <div class="song-info" v-if="songData.title">
        <h3>{{ songData.title }}</h3>
        <p>BPM: {{ songData.bpm }} | Duration: {{ songData.duration }}s</p>
      </div> -->

      <div class="center-message">
        <p>{{ currentMessage || beat0Message || '' }}</p>
      </div>

      <div class="play-controls">
        <!-- During processing -->
        <button v-if="isPlaying" class="cancel-btn" @click="cancelPlay">
          Cancel
        </button>
        
        <!-- After completion -->
        <template v-else-if="hasStarted">
          <button class="restart-btn" @click="restartPlay">
            Replay
          </button>
          <button class="back-btn" @click="goBack">
            Back
          </button>
        </template>
      </div>
    </div>

    <audio ref="audioPlayer" @ended="onAudioEnded" @canplay="onAudioReady"></audio>
  </BaseView>
</template>

<script>
import BaseView from '@/components/BaseView.vue'
export default {
  name: 'WorkingMemoryPlay',
  components: {
    BaseView
  },
  props: {
    trainingFile: {
      type: String,
      required: true
    },
    levelNumber: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      trainingFileData: {
        title: 'Working Memory Training',
        song: '',
        levels: []
      },
      songData: {},
      currentLevel: { level: 1, notes: [] },
      isLoading: false,
      isPlaying: false,
      hasStarted: false,
      buttonColors: Array(7).fill({ color: '#2c3e50' }),
      activeButtons: [],
      currentSequence: 0,
      playData: [],
      beatTimeouts: [],
      beatInterval: 600, // Default 100 BPM
      startTime: null,
      segmentButtons: {}, // Store random buttons for each segment
      currentExpectedSegment: null,
      currentMessage: '',
      showReport: false,
      reportData: null
    }
  },
  async mounted() {
    await this.loadTrainingFile()
    this.setupLevel()
    await this.loadSongData()
    // Auto start after all data is loaded
    this.startPlay()
  },
  beforeUnmount() {
    this.clearAllTimeouts()
  },
  methods: {
    async loadTrainingFile() {
      try {
        const response = await fetch(`/training/working-memory/${this.trainingFile}`)
        this.trainingFileData = await response.json()
      } catch (error) {
        console.error('Failed to load training file:', error)
        this.trainingFileData = {
          title: 'Training',
          song: '',
          levels: [{ level: 1, notes: [] }]
        }
      }
    },
    setupLevel() {
      this.currentLevel = this.trainingFileData.levels.find(
        level => level.level === this.levelNumber
      ) || { level: this.levelNumber, notes: [] }
    },
    async loadSongData() {
      if (!this.trainingFileData.song) return

      this.isLoading = true
      try {
        const response = await fetch(`/music/${this.trainingFileData.song}/info.json`)
        this.songData = await response.json()
        this.setupAudio()
      } catch (error) {
        console.error('Failed to load song data:', error)
        this.songData = { title: 'Unknown Song', bpm: 100, duration: 30 }
      } finally {
        this.isLoading = false
      }
    },
    setupAudio() {
      if (this.songData.bpm) {
        this.beatInterval = (60 / this.songData.bpm) * 1000
      }

      const audioPath = `/music/${this.trainingFileData.song}/music.mp3`
      this.$refs.audioPlayer.src = audioPath
    },
    async startPlay() {
      if (this.isLoading) return

      this.isPlaying = true
      this.hasStarted = true
      this.startTime = Date.now()
      this.playData = []
      this.currentSequence = 0

      try {
        await this.$refs.audioPlayer.play()
        this.setupNoteScheduling()
      } catch (error) {
        console.error('Failed to play audio:', error)
        this.setupNoteScheduling() // Continue without audio if needed
      }
    },
    stopPlay() {
      this.isPlaying = false
      this.$refs.audioPlayer.pause()
      this.clearAllTimeouts()
      this.buttonColors = Array(7).fill({ color: '#2c3e50' })
      this.activeButtons = []
      this.currentMessage = ''
      this.outputPlayResults()
    },
    cancelPlay() {
      this.isPlaying = false
      this.$refs.audioPlayer.pause()
      this.clearAllTimeouts()
      this.buttonColors = Array(7).fill({ color: '#2c3e50' })
      this.activeButtons = []
      this.currentMessage = ''
      console.log('Training cancelled by user')
    },
    restartPlay() {
      this.hasStarted = false
      this.isPlaying = false
      this.$refs.audioPlayer.currentTime = 0
      this.buttonColors = Array(7).fill({ color: '#2c3e50' })
      this.activeButtons = []
      this.clearAllTimeouts()
      this.segmentButtons = {}
      this.currentExpectedSegment = null
      this.currentMessage = ''
      this.showReport = false
      this.reportData = null
      // Immediately start playing again
      this.startPlay()
    },
    setupNoteScheduling() {
      console.log('Setting up working memory note scheduling:')
      console.log('BPM:', this.songData.bpm)
      console.log('First Beat Time:', this.songData.firstBeatTime, 's')
      console.log('Beat Interval:', this.beatInterval, 'ms')

      this.currentLevel.notes.forEach(note => {
        let beatTime

        if (note.beat === 0) {
          // Beat 0: from music start to beat 1
          beatTime = 0
        } else {
          // Regular beats: firstBeatTime + (beat - 1) * interval
          beatTime = this.songData.firstBeatTime * 1000 + (note.beat - 1) * this.beatInterval
        }

        console.log(`Beat ${note.beat}: scheduled at ${beatTime}ms for button ${note.button}`)

        const timeout = setTimeout(() => {
          this.activateNote(note)
        }, beatTime)

        this.beatTimeouts.push(timeout)
      })

      // Schedule message display if notes have message property
      this.currentLevel.notes.forEach(note => {
        if (note.message) {
          let beatTime

          if (note.beat === 0) {
            // Beat 0: from music start to beat 1
            beatTime = 0
          } else {
            // Regular beats: firstBeatTime + (beat - 1) * interval
            beatTime = this.songData.firstBeatTime * 1000 + (note.beat - 1) * this.beatInterval
          }

          const timeout = setTimeout(() => {
            this.currentMessage = note.message
            console.log(`Beat ${note.beat}: showing message "${note.message}"`)
          }, beatTime)

          this.beatTimeouts.push(timeout)
        }
      })
    },
    activateNote(note) {
      console.log(`Activating note: seg ${note.seg}, beat ${note.beat}, button ${note.button}`)

      if (note.button === 8) {
        // Show segment: generate random button for this segment and keep it lit until next segment
        const randomButton = Math.floor(Math.random() * 7) + 1 // 1-7
        this.segmentButtons[note.seg] = randomButton
        this.activeButtons = [randomButton]
        this.updateButtonColors()
        console.log(`Segment ${note.seg}: showing random button ${randomButton} from beat ${note.beat}`)

        // Schedule auto-deactivation at the next button=0 beat for this segment
        const nextInputBeat = this.findInputBeatForSegment(note.seg)
        if (nextInputBeat) {
          const deactivateTime = this.songData.firstBeatTime * 1000 + (nextInputBeat - 1) * this.beatInterval
          const currentTime = this.songData.firstBeatTime * 1000 + (note.beat - 1) * this.beatInterval
          const delay = deactivateTime - currentTime

          setTimeout(() => {
            this.activeButtons = []
            this.updateButtonColors()
            this.currentExpectedSegment = note.seg
            console.log(`Segment ${note.seg}: input phase started, expecting button ${randomButton}`)
          }, delay)
        }
      } else if (note.button === 0) {
        // Input phase already started by the timeout above
        // Just ensure we're in the right state and set timeout for miss detection
        if (this.currentExpectedSegment !== note.seg) {
          this.currentExpectedSegment = note.seg
          this.activeButtons = []
          this.updateButtonColors()
        }

        // Auto-timeout for this segment if no input received
        const nextBeat = this.getNextBeat(note.beat)
        if (nextBeat) {
          const timeoutDuration = (nextBeat - note.beat) * this.beatInterval
          setTimeout(() => {
            if (this.currentExpectedSegment === note.seg) {
              // No input received, record as miss
              this.recordSegmentResult(note.seg, null, 'miss')
              this.currentExpectedSegment = null
            }
          }, timeoutDuration)
        }
      } else if (note.button >= 1 && note.button <= 7) {
        // Specific button (if any custom segments need this)
        if (!this.activeButtons.includes(note.button)) {
          this.activeButtons.push(note.button)
        }
        this.updateButtonColors()
      }
    },
    updateButtonColors() {
      this.buttonColors = this.buttonColors.map((button, index) => ({
        color: this.activeButtons.includes(index + 1) ? '#FFD700' : '#2c3e50'
      }))
    },
    onButtonTap(buttonNumber) {
      if (!this.isPlaying || this.currentExpectedSegment === null) return

      const expectedButton = this.segmentButtons[this.currentExpectedSegment]
      const isCorrect = buttonNumber === expectedButton

      console.log(`Button ${buttonNumber} tapped for segment ${this.currentExpectedSegment}, expected ${expectedButton}: ${isCorrect ? 'CORRECT' : 'WRONG'}`)

      // Record the result
      this.recordSegmentResult(this.currentExpectedSegment, buttonNumber, isCorrect ? 'correct' : 'wrong')

      // Clear the expected segment
      this.currentExpectedSegment = null
    },
    onAudioEnded() {
      this.stopPlay()
    },
    onAudioReady() {
      console.log('Audio ready to play')
    },
    clearAllTimeouts() {
      this.beatTimeouts.forEach(timeout => clearTimeout(timeout))
      this.beatTimeouts = []
    },
    outputPlayResults() {
      const results = {
        song: this.trainingFileData.song || 'unknown',
        level: this.currentLevel.level,
        duration: Date.now() - this.startTime,
        data: this.playData,
        summary: this.calculateSummary()
      }

      console.log('=== WORKING MEMORY PLAY RESULTS ===')
      console.log(JSON.stringify(results, null, 2))
      
      // Show report on screen
      this.reportData = results
      this.showReport = true
    },
    getNextBeat(currentBeat) {
      // Find the next beat in the sequence
      const nextNote = this.currentLevel.notes.find(note => note.beat > currentBeat)
      return nextNote ? nextNote.beat : null
    },
    findInputBeatForSegment(segmentNumber) {
      // Find the beat where button=0 for this segment (input phase)
      const inputNote = this.currentLevel.notes.find(note => note.seg === segmentNumber && note.button === 0)
      return inputNote ? inputNote.beat : null
    },
    recordSegmentResult(segment, buttonTapped, result) {
      const record = {
        segment: segment,
        expectedButton: this.segmentButtons[segment],
        tappedButton: buttonTapped,
        result: result,
        timestamp: Date.now() - this.startTime
      }

      this.playData.push(record)
      console.log('Segment result recorded:', record)
    },
    calculateSummary() {
      const correct = this.playData.filter(d => d.result === 'correct').length
      const wrong = this.playData.filter(d => d.result === 'wrong').length
      const miss = this.playData.filter(d => d.result === 'miss').length
      const totalSegments = Math.max(...this.currentLevel.notes.map(note => note.seg), 0)

      return {
        totalSegments: totalSegments,
        attempted: this.playData.length,
        correct: correct,
        wrong: wrong,
        miss: miss,
        accuracy: this.playData.length > 0 ? Math.round((correct / this.playData.length) * 100) : 0,
        duration: this.songData.duration
      }
    },
    goBack() {
      this.$emit('go-back')
    },
    getBeat0Message() {
      const beat0Note = this.currentLevel.notes.find(note => note.beat === 0 && note.message)
      return beat0Note ? beat0Note.message : ''
    }
  }
}
</script>

