<template>
  <BaseMenu 
    :title="trainingData.title" 
    back-button-text="Back to Working Memory"
    @go-back="goBack"
  >
    <template #menu-items>
      <div class="level-subtitle">
        <p>Select Level</p>
      </div>
      <button v-for="level in trainingData.levels" :key="level.level" class="menu-btn"
        @click="navigateToLevel(level.level)">
        Level {{ level.level }}
      </button>
    </template>
  </BaseMenu>
</template>

<script>
import BaseMenu from '@/components/BaseMenu.vue'

export default {
  name: 'WorkingMemoryLevelMenu',
  components: {
    BaseMenu
  },
  props: {
    trainingFile: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      trainingData: {
        title: 'Working Memory Training',
        levels: []
      }
    }
  },
  async mounted() {
    await this.loadTrainingData()
  },
  methods: {
    async loadTrainingData() {
      try {
        const response = await fetch(`/training/working-memory/${this.trainingFile}`)
        this.trainingData = await response.json()
      } catch (error) {
        console.error('Failed to load training data:', error)
        this.trainingData = {
          title: 'Training',
          levels: [{ level: 1 }]
        }
      }
    },
    navigateToLevel(levelNumber) {
      this.$emit('navigate-to-level', {
        file: this.trainingFile,
        level: levelNumber
      })
    },
    goBack() {
      this.$emit('go-back')
    }
  }
}
</script>

<style scoped>
@import '@/assets/menu-button.css';

.level-subtitle {
  text-align: center;
  margin-bottom: 2vmin;
}

.level-subtitle p {
  margin: 0;
  font-size: 3vmin;
  opacity: 0.8;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}
</style>