<template>
  <BaseMenu 
    title="Select One Training"
    @go-back="goBack"
  >
    <template #menu-items>
      <button class="menu-btn" @click="navigateToTraining('simple-reaction-training')" disabled>
        Simple Reaction Time
      </button>
      <button class="menu-btn" @click="navigateToTraining('working-memory-training')">
        Working Memory
      </button>
      <button class="menu-btn" @click="navigateToTraining('trail-making-training')" disabled>
        Trail Making
      </button>
    </template>
  </BaseMenu>
</template>

<script>
import BaseMenu from '@/components/BaseMenu.vue'

export default {
  name: 'TrainingMenu',
  components: {
    BaseMenu
  },
  methods: {
    navigateToTraining(training) {
      if (training === 'simple-reaction-training') {
        this.$emit('navigate-to-srt-menu')
      } else if (training === 'working-memory-training') {
        this.$emit('navigate-to-wm-menu')
      }
    },
    goBack() {
      console.log('Going back to main menu')
      this.$emit('navigate-to-main')
    }
  }
}
</script>

<style scoped>
@import '@/assets/menu-button.css';
</style>