<template>
  <BaseView :actionButtons="buttonColors" @action-button-click="onButtonTap">
    <div class="training-header">
      <h1>{{ trainingFileData.title }}</h1>
      <p>Level {{ currentLevel.level }}</p>
    </div>

    <div class="training-content">
      <div class="training-status">
        <p v-if="isLoading">Loading song data...</p>
        <p v-else-if="isPlaying"> Training in progress...</p>
        <p v-else>Ready to train!</p>
      </div>

      <div class="song-info" v-if="songData.title">
        <h3>{{ songData.title }}</h3>
        <p>BPM: {{ songData.bpm }} | Duration: {{ songData.duration }}s</p>
      </div>

      <div class="level-details" v-if="!isPlaying && !hasStarted">
        <h3>Level Notes:</h3>
        <div v-for="(note, index) in currentLevel.notes" :key="index" class="note-item">
          <span>Beat {{ note.beat }}: {{ note.button }}</span>
        </div>
      </div>

      <div class="training-controls">
        <button v-if="!isPlaying && !hasStarted" class="start-btn" @click="startTraining" :disabled="isLoading">
          {{ isLoading ? 'Loading...' : 'Start Training' }}
        </button>
        <button v-else-if="isPlaying" class="stop-btn" @click="stopTraining">
          Stop Training
        </button>
        <button v-else class="restart-btn" @click="restartTraining">
          Play Again
        </button>
      </div>
    </div>

    <div class="back-button">
      <button class="back-btn" @click="goBack" :disabled="isPlaying">
        Back to Levels
      </button>
    </div>

    <audio ref="audioPlayer" @ended="onAudioEnded" @canplay="onAudioReady"></audio>
  </BaseView>
</template>

<script>
import BaseView from '@/components/BaseView.vue'

export default {
  name: 'SimpleReactionTimeTraining',
  components: {
    BaseView
  },
  props: {
    trainingFile: {
      type: String,
      required: true
    },
    levelNumber: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      trainingFileData: {
        title: '',
        song: '',
        levels: []
      },
      currentLevel: {
        level: 1,
        notes: []
      },
      songData: {
        title: '',
        bpm: 0,
        duration: 0,
        notes: []
      },
      isLoading: true,
      isPlaying: false,
      audioReady: false,
      hasStarted: false,
      activeButtons: [],
      beatTimeouts: [],
      startTime: null,
      beatInterval: 0,
      buttonColors: [
        { color: '#2c3e50' },
        { color: '#2c3e50' },
        { color: '#2c3e50' },
        { color: '#2c3e50' },
        { color: '#2c3e50' },
        { color: '#2c3e50' },
        { color: '#2c3e50' }
      ],
      trainingData: [],
      expectedNotes: [],
      currentActiveNote: null
    }
  },
  async mounted() {
    await this.loadTrainingData()
    this.setCurrentLevel()
    await this.loadSongData()
    this.autoStartTraining()
  },
  methods: {
    async loadTrainingData() {
      try {
        const response = await fetch(`/training/srt/${this.trainingFile}`)
        this.trainingFileData = await response.json()
      } catch (error) {
        console.error('Failed to load training data:', error)
        this.trainingFileData = {
          title: 'Training',
          song: '',
          levels: [{ level: 1, notes: [] }]
        }
      }
    },
    async loadSongData() {
      if (!this.trainingFileData.song) return

      try {
        const response = await fetch(`/music/${this.trainingFileData.song}/info.json`)
        this.songData = await response.json()
        this.setupAudio()
      } catch (error) {
        console.error('Failed to load song data:', error)
        this.songData = { title: 'Unknown Song', bpm: 120, duration: 30, notes: [] }
      }
    },
    setupAudio() {
      const audioPath = `/music/${this.trainingFileData.song}/music.mp3`
      this.$refs.audioPlayer.src = audioPath
      this.$refs.audioPlayer.load()
    },
    setCurrentLevel() {
      this.currentLevel = this.trainingFileData.levels.find(
        level => level.level === this.levelNumber
      ) || this.trainingFileData.levels[0] || { level: 1, notes: [] }
    },
    autoStartTraining() {
      setTimeout(() => {
        this.isLoading = false
        this.startTraining()
      }, 1000)
    },
    startTraining() {
      if (this.audioReady && !this.isPlaying) {
        this.hasStarted = true
        this.isPlaying = true
        this.calculateBeatTiming()
        this.initializeTrainingData()
        this.setupNoteScheduling()
        this.startTime = Date.now()
        this.$refs.audioPlayer.play()
      }
    },
    initializeTrainingData() {
      this.trainingData = []
      this.expectedNotes = []
      this.currentActiveNote = null
    },
    stopTraining() {
      this.isPlaying = false
      this.clearAllTimeouts()
      this.activeButtons = []
      this.updateButtonColors()
      this.$refs.audioPlayer.pause()
      this.$refs.audioPlayer.currentTime = 0
    },
    restartTraining() {
      this.hasStarted = false
      this.clearAllTimeouts()
      this.activeButtons = []
      this.updateButtonColors()
      this.$refs.audioPlayer.currentTime = 0
    },
    calculateBeatTiming() {
      // Convert BPM to beat interval in milliseconds
      this.beatInterval = (60 / this.songData.bpm) * 1000
    },
    setupNoteScheduling() {
      console.log('Setting up note scheduling:')
      console.log('BPM:', this.songData.bpm)
      console.log('First Beat Time:', this.songData.firstBeatTime, 's')
      console.log('Beat Interval:', this.beatInterval, 'ms')

      this.currentLevel.notes.forEach(note => {
        // Beat 1 = firstBeatTime, Beat 2 = firstBeatTime + beatInterval, etc.
        const beatTime = this.songData.firstBeatTime * 1000 + (note.beat - 1) * this.beatInterval

        console.log(`Beat ${note.beat}: scheduled at ${beatTime}ms (${beatTime / 1000}s)`)

        const timeout = setTimeout(() => {
          console.log(`Activating button for beat ${note.beat} at ${Date.now() - this.startTime}ms after start`)
          this.activateButton(note)
        }, beatTime)

        this.beatTimeouts.push(timeout)
      })
    },
    activateButton(note) {
      let buttonNumber

      if (note.button === 'rnd') {
        buttonNumber = Math.floor(Math.random() * 7) + 1
      } else {
        buttonNumber = parseInt(note.button) || 1
      }

      const expectedTime = Date.now()
      const expectedNote = {
        beat: note.beat,
        buttonIndex: buttonNumber,
        expectedTime: expectedTime,
        activated: true,
        tapped: false
      }

      this.expectedNotes.push(expectedNote)
      this.currentActiveNote = expectedNote
      this.activeButtons = [buttonNumber]
      this.updateButtonColors()

      // Auto-deactivate after 2 seconds if not tapped
      setTimeout(() => {
        if (!expectedNote.tapped) {
          // Record as miss
          this.recordTrainingData(expectedNote.buttonIndex, null, 'miss')
          expectedNote.tapped = true // Mark as processed
        }
        this.activeButtons = this.activeButtons.filter(btn => btn !== buttonNumber)
        this.updateButtonColors()
        if (this.currentActiveNote === expectedNote) {
          this.currentActiveNote = null
        }
      }, 2000)
    },
    updateButtonColors() {
      this.buttonColors = this.buttonColors.map((button, index) => ({
        color: this.activeButtons.includes(index + 1) ? '#FFD700' : '#2c3e50'
      }))
    },
    onButtonTap(buttonNumber) {
      const tapTime = Date.now()

      if (this.activeButtons.includes(buttonNumber) && this.currentActiveNote) {
        // Correct button tapped
        const timeDiff = tapTime - this.currentActiveNote.expectedTime
        const result = this.calculateAccuracy(timeDiff)

        this.recordTrainingData(buttonNumber, timeDiff, result)
        this.currentActiveNote.tapped = true

        console.log(`Button ${buttonNumber} tapped: ${result} (${timeDiff}ms)`)

        this.activeButtons = this.activeButtons.filter(btn => btn !== buttonNumber)
        this.updateButtonColors()
        this.currentActiveNote = null
      } else {
        // Wrong button or no active button
        this.recordTrainingData(buttonNumber, null, 'miss')
        console.log(`Button ${buttonNumber} tapped incorrectly`)
      }
    },
    calculateAccuracy(timeDiff) {
      const absTimeDiff = Math.abs(timeDiff)

      if (absTimeDiff <= 100) {
        return 'perfect' // Within 100ms
      } else if (absTimeDiff <= 300) {
        return 'good'    // Within 300ms
      } else {
        return 'miss'    // Too late/early
      }
    },
    recordTrainingData(buttonIndex, timeDiff, result) {
      const record = {
        buttonIndex: buttonIndex,
        timeDiff: timeDiff,
        result: result,
        timestamp: Date.now() - this.startTime
      }

      this.trainingData.push(record)
      console.log('Training data recorded:', record)
    },
    clearAllTimeouts() {
      this.beatTimeouts.forEach(timeout => clearTimeout(timeout))
      this.beatTimeouts = []
    },
    onAudioReady() {
      this.audioReady = true
    },
    onAudioEnded() {
      this.isPlaying = false
      this.clearAllTimeouts()
      this.activeButtons = []
      this.updateButtonColors()
      this.outputTrainingResults()
    },
    outputTrainingResults() {
      const results = {
        song: this.trainingFileData.song || 'unknown',
        level: this.currentLevel.level,
        duration: Date.now() - this.startTime,
        data: this.trainingData,
        summary: this.calculateSummary()
      }

      console.log('=== TRAINING RESULTS ===')
      console.log(JSON.stringify(results, null, 2))
    },
    calculateSummary() {
      const total = this.trainingData.length
      const perfect = this.trainingData.filter(d => d.result === 'perfect').length
      const good = this.trainingData.filter(d => d.result === 'good').length
      const miss = this.trainingData.filter(d => d.result === 'miss').length

      return {
        total: total,
        perfect: perfect,
        good: good,
        miss: miss,
        accuracy: total > 0 ? Math.round(((perfect + good) / total) * 100) : 0
      }
    },
    goBack() {
      if (!this.isPlaying) {
        this.stopTraining()
        this.$emit('go-back')
      }
    }
  },
  watch: {
    levelNumber() {
      this.setCurrentLevel()
    }
  }
}
</script>

