<template>
  <BaseMenu title="Select One Song" @go-back="goBack">
    <template #menu-items>
      <button v-for="item in trainingItems" :key="item.order || item.title" class="menu-btn"
        @click="navigateToTraining(item)" :disabled="!item.file">
        {{ item.title }}
      </button>
    </template>
  </BaseMenu>
</template>

<script>
import BaseMenu from '@/components/BaseMenu.vue'

export default {
  name: 'WorkingMemoryMenu',
  components: {
    BaseMenu
  },
  data() {
    return {
      trainingItems: []
    }
  },
  async mounted() {
    await this.loadTrainingList()
  },
  methods: {
    async loadTrainingList() {
      try {
        const response = await fetch('/training/working-memory/list.json')
        this.trainingItems = await response.json()
      } catch (error) {
        console.error('Failed to load working memory training list:', error)
        this.trainingItems = [
          { title: 'Failed to load training list' }
        ]
      }
    },
    async navigateToTraining(item) {
      if (item.file) {
        // Check if this training file has only one level
        try {
          const response = await fetch(`/training/working-memory/${item.file}`)
          const trainingData = await response.json()

          if (trainingData.levels && trainingData.levels.length === 1) {
            // Skip level menu and go directly to play with level 1
            this.$emit('navigate-to-play', {
              file: item.file,
              level: 1
            })
          } else {
            // Multiple levels, show level menu
            this.$emit('navigate-to-level-menu', {
              file: item.file
            })
          }
        } catch (error) {
          console.error('Failed to load training file:', error)
          // Fallback to level menu on error
          this.$emit('navigate-to-level-menu', {
            file: item.file
          })
        }
      }
    },
    goBack() {
      this.$emit('go-back')
    }
  }
}
</script>

<style scoped>
@import '@/assets/menu-button.css';
</style>