<template>
  <div id="app">
    <MainMenu v-if="currentView === 'main'" @navigate-to-task="navigateToTask"
      @navigate-to-training="navigateToTraining" />
    <TaskMenu v-if="currentView === 'task'" @navigate-to-main="navigateToMain"
      @navigate-to-simple-reaction="navigateToSimpleReaction" />
    <TrainingMenu v-if="currentView === 'training'" @navigate-to-main="navigateToMain" @navigate-to-srt-menu="navigateToSrtMenu" @navigate-to-wm-menu="navigateToWmMenu" />
    <WorkingMemoryMenu v-if="currentView === 'wm-menu'" @go-back="navigateBackToTraining" @navigate-to-level-menu="navigateToWmLevelMenu" @navigate-to-play="navigateToWmPlay" />
    <WorkingMemoryLevelMenu v-if="currentView === 'wm-level-menu'" :trainingFile="selectedTrainingFile" @go-back="navigateBackToWmMenu" @navigate-to-level="navigateToWmLevel" />
    <WorkingMemoryPlay v-if="currentView === 'wm-play'" :trainingFile="selectedTrainingFile" :levelNumber="selectedLevel" @go-back="navigateBackFromWmPlay" />
    <SimpleReactionTimeMenu v-if="currentView === 'srt-menu'" @go-back="navigateBackToTraining" @navigate-to-level-menu="navigateToLevelMenu" />
    <SimpleReactionTimeLevelMenu v-if="currentView === 'srt-level-menu'" :trainingFile="selectedTrainingFile" @go-back="navigateBackToSrtMenu" @navigate-to-level="navigateToLevel" />
    <SimpleReactionTimeTraining v-if="currentView === 'srt-training'" :trainingFile="selectedTrainingFile" :levelNumber="selectedLevel" @go-back="navigateBackToLevelMenu" @start-training="startTraining" />
    <SimpleReactionTimeTest v-if="currentView === 'simple-reaction'" @go-back="navigateBackToTask"
      @show-result="navigateToTestResult" />
    <SimpleReactionTimeTestResult v-if="currentView === 'test-result'" :reportData="testReportData"
      @try-again="handleTryAgain" @go-back="navigateBackToTask" />
  </div>
</template>

<script>
import MainMenu from './views/MainMenu.vue'
import TaskMenu from './views/task/TaskMenu.vue'
import TrainingMenu from './views/training/TrainingMenu.vue'
import SimpleReactionTimeMenu from './views/training/srt/SimpleReactionTimeMenu.vue'
import SimpleReactionTimeLevelMenu from './views/training/srt/SimpleReactionTimeLevelMenu.vue'
import SimpleReactionTimeTraining from './views/training/srt/SimpleReactionTimeTraining.vue'
import SimpleReactionTimeTest from './views/task/SimpleReactionTimeTest.vue'
import SimpleReactionTimeTestResult from './views/task/SimpleReactionTimeTestResult.vue'
import WorkingMemoryMenu from './views/training/working-memory/Menu.vue'
import WorkingMemoryLevelMenu from './views/training/working-memory/LevelMenu.vue'
import WorkingMemoryPlay from './views/training/working-memory/Play.vue'

export default {
  name: 'App',
  components: {
    MainMenu,
    TaskMenu,
    TrainingMenu,
    SimpleReactionTimeMenu,
    SimpleReactionTimeLevelMenu,
    SimpleReactionTimeTraining,
    SimpleReactionTimeTest,
    SimpleReactionTimeTestResult,
    WorkingMemoryMenu,
    WorkingMemoryLevelMenu,
    WorkingMemoryPlay
  },
  data() {
    return {
      currentView: 'main',
      testReportData: [],
      selectedTrainingFile: null,
      selectedLevel: null,
      wmSkippedLevelMenu: false
    }
  },
  methods: {
    navigateToTask() {
      this.currentView = 'task'
    },
    navigateToTraining() {
      this.currentView = 'training'
    },
    navigateToMain() {
      this.currentView = 'main'
    },
    navigateToSimpleReaction() {
      this.currentView = 'simple-reaction'
    },
    navigateBackToTask() {
      this.currentView = 'task'
    },
    navigateToTestResult(reportData) {
      this.testReportData = reportData
      this.currentView = 'test-result'
    },
    handleTryAgain() {
      this.currentView = 'simple-reaction'
    },
    navigateToSrtMenu() {
      this.currentView = 'srt-menu'
    },
    navigateBackToTraining() {
      this.currentView = 'training'
    },
    navigateToLevelMenu(data) {
      this.selectedTrainingFile = data.file
      this.currentView = 'srt-level-menu'
    },
    navigateBackToSrtMenu() {
      this.currentView = 'srt-menu'
    },
    navigateToLevel(data) {
      this.selectedLevel = data.level
      this.currentView = 'srt-training'
    },
    navigateBackToLevelMenu() {
      this.currentView = 'srt-level-menu'
    },
    startTraining(data) {
      // TODO: Navigate to actual training session
      console.log('Start training with:', data)
    },
    navigateToWmMenu() {
      this.currentView = 'wm-menu'
    },
    navigateToWmLevelMenu(data) {
      this.selectedTrainingFile = data.file
      this.wmSkippedLevelMenu = false
      this.currentView = 'wm-level-menu'
    },
    navigateBackToWmMenu() {
      this.currentView = 'wm-menu'
    },
    navigateToWmLevel(data) {
      this.selectedLevel = data.level
      this.currentView = 'wm-play'
    },
    navigateBackToWmLevelMenu() {
      this.currentView = 'wm-level-menu'
    },
    navigateToWmPlay(data) {
      this.selectedTrainingFile = data.file
      this.selectedLevel = data.level
      this.wmSkippedLevelMenu = true
      this.currentView = 'wm-play'
    },
    navigateBackFromWmPlay() {
      if (this.wmSkippedLevelMenu) {
        // Came directly from menu, go back to menu
        this.currentView = 'wm-menu'
      } else {
        // Came from level menu, go back to level menu
        this.currentView = 'wm-level-menu'
      }
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  padding: 0;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  -webkit-touch-callout: none;
  touch-action: manipulation;
}

* {
  -webkit-user-drag: none;
  -webkit-touch-callout: none;
  user-select: none;
}
</style>
