<template>
  <BaseMenu 
    title="Molly Move" 
    :show-back="false"
  >
    <template #menu-items>
      <button class="menu-btn" @click="navigateTo('training')">
        Training
      </button>
      <button class="menu-btn" @click="navigateTo('task')" disabled>
        Task
      </button>
      <button class="menu-btn" @click="navigateTo('setting')" disabled>
        Setting
      </button>
    </template>
  </BaseMenu>
</template>

<script>
import BaseMenu from '@/components/BaseMenu.vue'

export default {
  name: 'MainMenu',
  components: {
    BaseMenu
  },
  methods: {
    navigateTo(section) {
      console.log(`Navigating to: ${section}`)
      if (section === 'task') {
        this.$emit('navigate-to-task')
      } else if (section === 'training') {
        this.$emit('navigate-to-training')
      }
    }
  }
}
</script>

<style scoped>
@import '../assets/menu-button.css';
</style>