<template>
  <BaseView>
    <div class="results">
      <h2>Test Results</h2>
      <div class="stats">
        <div class="stat-item">
          <span class="stat-label">Total Taps:</span>
          <span class="stat-value">{{ reportData.length }}</span>
        </div>
        <div class="stat-item">
          <span class="stat-label">Average Time:</span>
          <span class="stat-value">{{ getAverageReactionTime() }}ms</span>
        </div>
        <div class="stat-item">
          <span class="stat-label">Best Time:</span>
          <span class="stat-value">{{ getBestReactionTime() }}ms</span>
        </div>
        <div class="stat-item">
          <span class="stat-label">Worst Time:</span>
          <span class="stat-value">{{ getWorstReactionTime() }}ms</span>
        </div>
      </div>
      <p class="result-rating">{{ getRating() }}</p>
      
      <div class="detailed-results">
        <h3>Tap Details</h3>
        <div class="tap-list">
          <div v-for="(tap, index) in reportData" :key="index" class="tap-item">
            <span>Button {{ tap.buttonIndex }}: {{ tap.reactionTime }}ms</span>
          </div>
        </div>
      </div>
    </div>
    
    <div class="controls">
      <button @click="tryAgain" class="restart-btn">
        Try Again
      </button>
      <button @click="goBack" class="back-btn">
        Back to Tasks
      </button>
    </div>
  </BaseView>
</template>

<script>
import BaseView from '../../components/BaseView.vue'

export default {
  name: 'SimpleReactionTimeTestResult',
  components: {
    BaseView
  },
  props: {
    reportData: {
      type: Array,
      required: true
    }
  },
  methods: {
    getAverageReactionTime() {
      if (this.reportData.length === 0) return 0
      const sum = this.reportData.reduce((total, tap) => total + tap.reactionTime, 0)
      return Math.round(sum / this.reportData.length)
    },
    
    getBestReactionTime() {
      if (this.reportData.length === 0) return 0
      return Math.min(...this.reportData.map(tap => tap.reactionTime))
    },
    
    getWorstReactionTime() {
      if (this.reportData.length === 0) return 0
      return Math.max(...this.reportData.map(tap => tap.reactionTime))
    },
    
    getRating() {
      const avgTime = this.getAverageReactionTime()
      if (!avgTime) return ''
      
      if (avgTime < 200) return 'Excellent! Very fast average reaction time.'
      if (avgTime < 300) return 'Good average reaction time.'
      if (avgTime < 400) return 'Average reaction time.'
      if (avgTime < 500) return 'Below average reaction time.'
      return 'Slow average reaction time. Keep practicing!'
    },
    
    tryAgain() {
      this.$emit('try-again')
    },
    
    goBack() {
      this.$emit('go-back')
    }
  }
}
</script>

