<template>
  <button class="tap-button" :class="{ clicked: isClicked }" :style="{
    backgroundColor: isClicked ? '#FFD700' : color,
    transform: `rotate(${angle}deg) translate(${calculatedRadius}) rotate(-${angle}deg) ${isClicked ? 'scale(1.3)' : 'scale(1)'}`
  }" @click="handleClick">
    {{ index }}
  </button>
</template>

<script>
export default {
  name: 'TapButton',
  props: {
    index: {
      type: Number,
      required: true
    },
    angle: {
      type: Number,
      required: true
    },
    radius: {
      type: Number,
      default: 360
    },
    color: {
      type: String,
      default: '#2c3e50'
    }
  },
  data() {
    return {
      calculatedRadius: '360px',
      isClicked: false
    }
  },
  methods: {
    handleClick() {
      this.isClicked = true
      setTimeout(() => {
        this.isClicked = false
      }, 150)
      this.$emit('click', this.index)
    }
  },
  mounted() {
    // Calculate radius based on viewport dimensions
    // Circle is 60vmin, so radius should be 30vmin + button size + margin
    const vmin = Math.min(window.innerWidth, window.innerHeight)
    const circleRadius = (30 * vmin) / 100 // 30vmin in pixels
    const buttonSize = (6.7 * vmin) / 200 // Half of button size in vmin units
    const margin = (2 * vmin) / 100 // 2vmin margin to ensure buttons are outside
    
    this.calculatedRadius = `${circleRadius + buttonSize + margin}px`
  }
}
</script>

<style scoped>
.tap-button {
  position: absolute;
  width: 6.7vmin;
  height: 6.7vmin;
  border-radius: 50%;
  border: 2px solid white;
  color: white;
  font-weight: bold;
  font-size: 2.7vmin;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transform-origin: center;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  -webkit-touch-callout: none;
  touch-action: manipulation;
}

.tap-button:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.tap-button.clicked {
  box-shadow: 0 0 25px rgba(255, 215, 0, 0.7), 0 0 50px rgba(255, 215, 0, 0.5);
  border-color: #FFD700;
}
</style>