<template>
  <BaseView>
    <div class="menu-header">
      <h1>Simple Reaction Time Training</h1>
    </div>

    <div class="menu-options">
      <button v-for="level in trainingLevels" :key="level.id" class="menu-btn" @click="navigateToTraining(level.id)"
        :disabled="!level.enabled">
        {{ level.title }}
      </button>
    </div>

    <div class="back-button">
      <button class="back-btn" @click="goBack">
        Back to Training
      </button>
    </div>
  </BaseView>
</template>

<script>
import BaseView from '@/components/BaseView.vue'

export default {
  name: 'SimpleReactionTimeMenu',
  components: {
    BaseView
  },
  data() {
    return {
      trainingLevels: []
    }
  },
  async mounted() {
    await this.loadTrainingLevels()
  },
  methods: {
    async loadTrainingLevels() {
      try {
        const response = await fetch('/training/srt/list.json')
        const levels = await response.json()
        // Transform the JSON data to match expected format
        this.trainingLevels = levels.map((level, index) => ({
          id: level.file ? level.file.replace('.json', '') : `level-${index}`,
          title: level.title,
          enabled: !!level.file, // Enable only if file is specified
          file: level.file
        }))
      } catch (error) {
        console.error('Failed to load training levels:', error)
        // Fallback data if JSON fails to load
        this.trainingLevels = [
          { id: 'beginner', title: 'Beginner', enabled: true },
          { id: 'intermediate', title: 'Intermediate', enabled: false },
          { id: 'advanced', title: 'Advanced', enabled: false }
        ]
      }
    },
    navigateToTraining(levelId) {
      const trainingLevel = this.trainingLevels.find(t => t.id === levelId)
      if (trainingLevel && trainingLevel.enabled) {
        this.$emit('navigate-to-level-menu', { id: levelId, file: trainingLevel.file })
      }
    },
    goBack() {
      this.$emit('go-back')
    }
  }
}
</script>

