<template>
  <BaseView :actionButtons="tapButtons" @action-button-click="handleTapButtonClick">
    <div class="test-header">
      <h1>Simple Reaction Time Test</h1>
      <p v-if="!testStarted && !testCompleted">Click "Start Test" and wait for a button to light up, then tap it as fast
        as you can!</p>
      <p v-if="testStarted && timeLeft > 0" class="timer">Time: {{ timeLeft }}s</p>
    </div>

    <div class="controls">
      <button v-if="!testStarted && !testCompleted" @click="startTest" class="start-btn">
        Start Test
      </button>
      <button v-if="testCompleted" @click="resetTest" class="restart-btn">
        Try Again
      </button>
      <button @click="goBack" class="back-btn">
        Back to Tasks
      </button>
    </div>

  </BaseView>
</template>

<script>
import BaseView from '../../components/BaseView.vue'

export default {
  name: 'SimpleReactionTimeTest',
  components: {
    BaseView
  },
  data() {
    return {
      testDuration: 30, // Test duration in seconds
      testStarted: false,
      testCompleted: false,
      timeLeft: 0,
      startTime: null,
      reactionTime: null,
      timeout: null,
      gameTimer: null,
      targetButtonIndex: null,
      tapButtons: Array(7).fill({ color: '#2c3e50' }),
      reportData: []
    }
  },
  methods: {
    startTest() {
      this.testStarted = true
      this.testCompleted = false
      this.timeLeft = this.testDuration
      this.reactionTime = null
      this.targetButtonIndex = null
      this.tapButtons = Array(7).fill({ color: '#2c3e50' })
      this.reportData = []

      this.startGameTimer()
      this.scheduleNextTarget()
    },

    startGameTimer() {
      this.gameTimer = setInterval(() => {
        this.timeLeft--
        if (this.timeLeft <= 0) {
          this.endTest()
        }
      }, 1000)
    },

    scheduleNextTarget() {
      if (!this.testStarted || this.timeLeft <= 0) return

      // Random delay between 1-3 seconds
      const delay = Math.random() * 2000 + 1000

      this.timeout = setTimeout(() => {
        this.showRandomTarget()
      }, delay)
    },

    showRandomTarget() {
      if (!this.testStarted || this.timeLeft <= 0) return

      // Choose random button index (0-6)
      this.targetButtonIndex = Math.floor(Math.random() * 7)

      // Update tap buttons array to light up the target
      this.tapButtons = this.tapButtons.map((button, index) => ({
        color: index === this.targetButtonIndex ? '#4ecdc4' : '#2c3e50'
      }))

      this.startTime = Date.now()

      // Auto-hide after 2 seconds if not clicked
      this.autoHideTimeout = setTimeout(() => {
        this.hideTarget()
      }, 2000)
    },

    hideTarget() {
      this.targetButtonIndex = null
      this.tapButtons = Array(7).fill({ color: '#2c3e50' })

      if (this.autoHideTimeout) {
        clearTimeout(this.autoHideTimeout)
        this.autoHideTimeout = null
      }

      // Schedule next target
      this.scheduleNextTarget()
    },

    handleTapButtonClick(buttonIndex) {
      if (!this.testStarted || this.targetButtonIndex === null) return

      if (buttonIndex === this.targetButtonIndex + 1) { // +1 because button indices start from 1
        // Correct button tapped
        const currentReactionTime = Date.now() - this.startTime

        // Collect test data
        this.reportData.push({
          buttonIndex: buttonIndex,
          reactionTime: currentReactionTime,
          timestamp: Date.now()
        })

        // Store the most recent reaction time for display
        this.reactionTime = currentReactionTime

        // Hide current target and schedule next one
        this.hideTarget()
      }
    },

    endTest() {
      this.testStarted = false
      this.testCompleted = true
      this.clearAllTimers()
      this.tapButtons = Array(7).fill({ color: '#2c3e50' })

      // Emit result data to parent
      this.$emit('show-result', this.reportData)
    },

    resetTest() {
      this.testStarted = false
      this.testCompleted = false
      this.timeLeft = this.testDuration
      this.reactionTime = null
      this.targetButtonIndex = null
      this.tapButtons = Array(7).fill({ color: '#2c3e50' })
      this.reportData = []
      this.clearAllTimers()
    },

    clearAllTimers() {
      if (this.gameTimer) {
        clearInterval(this.gameTimer)
        this.gameTimer = null
      }
      if (this.timeout) {
        clearTimeout(this.timeout)
        this.timeout = null
      }
      if (this.autoHideTimeout) {
        clearTimeout(this.autoHideTimeout)
        this.autoHideTimeout = null
      }
    },

    goBack() {
      this.$emit('go-back')
    }
  },

  mounted() {
    this.resetTest()
  },

  beforeUnmount() {
    this.clearAllTimers()
  }
}
</script>

